
import EncounterMixinVue from "@/views/EncounterMixin.vue";
import { FieldType } from "@/components/Forms/BaseFormElements";
import { defineComponent } from "vue";
import { Field } from "@/components/Forms/FieldInterface";
import { IonPage } from "@ionic/vue";
import { HtsCircumcisionService } from "@/apps/HTS/services/hts_circumcision_service";

export default defineComponent({
  components: { IonPage },
  mixins: [EncounterMixinVue],
  data: () => ({
    service: {} as HtsCircumcisionService,
  }),
  watch: {
    ready: {
      handler(ready: boolean) {
        if (ready) {
          this.service = new HtsCircumcisionService(
            this.patientID,
            this.providerID
          );
          this.fields = [this.htsMaleCircumcisedField()];
        }
      },
      immediate: true,
    },
  },
  methods: {
    onFinish(formData: any, computedData: any) {
      this.saveFormEncounterAndProceed({
        encounterTypeID: this.service.encounterTypeID,
        formData,
        computedData,
      })
    },
    htsMaleCircumcisedField(): Field {
      return {
        id: "hts_male_circumcised",
        helpText: "Circumcision status",
        type: FieldType.TT_YES_NO,
        isRequired: () => true,
        finalComputedValue: (v: any) => {
          return {
            obs: this.service.buildValueCoded("Circumcision status", v),
          };
        },
        options: () => {
          return [
            {
              label: "Is client circumcised?",
              value: "",
              values: this.yesNoOptions(),
            },
          ];
        },
      };
    },
  },
});
